body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.wrapper{
  width:100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.main{
  margin-top: 10%;
}
.contacts{
  margin-top: 15%;
  text-align: center;
}
.btn{
  border:3px solid rgb(79, 173, 255);
  background-color: white;
  color: rgb(79, 173, 255);
  padding: 10px 25px;
  font-size: 20px;
  font-weight: 500;
  transition: all 0.2s ease;
}
.btn:hover{
  border:3px solid rgb(79, 173, 255);
  background-color: rgb(79, 173, 255);
  color: white;
  padding: 10px 25px;
  font-size: 20px;
  font-weight: 500;
}
.btn:active{
  background-color: rgb(12, 142, 255);
}
.nav-btn{
  position: absolute;
  top:50px;
  right: 100px;
  padding: 15px 25px;
  border-radius: 0px;
  background-color: rgb(79, 173, 255);
  color:white;
  border: none;
  font-size: 20px;
  font-weight: 500;
  transition: all 0.2s ease;
  cursor: pointer;
}
.nav-btn:hover{
  scale:1.05;
}
h1{
  color:rgb(24, 145, 251);
}
h2{
  color: rgb(78, 173, 255);
  margin-top: 50px;
}
p{
  color:#696969;
  font-size: 20px;
}
input{
  margin-bottom: 30px;
  margin-right: 10px;
  padding: 15px 25px;
  border-radius: 0px;
  border:3px solid rgb(79, 173, 255);
  font-size: 20px;
}
.notFound{
  margin-top: 20%;
  text-align: center;
}